body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    height: 100vh;
}

div#root {
    height: 100%;
}

.welcomePageTitle {
    color: blue;
    font-size: 40px;
    font-family: fantasy;
}

.signIn {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
    text-align: center;
    -ms-flex-pack: center;
    justify-content: center;
    width: 50%;
    height: 90vh;
    margin: 0 auto;
}

h1.signUp, .passForgot {
    margin-top: 3%;
}

.signupPassFrg {
    height: 20vh;
}

.signInForm {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    padding: 10px;
}

input {
    width: 100%;
    padding: 13px 12px;
    margin: 5px;
    outline: none;
    border: none;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border-bottom: 1px solid #00000021;
    font-size: 16px;
}

button.signin {
    background-color: red;
    color: white;
    font-size: 20px;
    outline: none;
}

button.signin:hover {
    background-color: rgba(255, 0, 0, 0.507);
    color: white;
}

a {
    text-decoration: none;
    color: blue;
}

@media (min-width: 320px) and (max-width: 480px) {
    div#root {
        height: 100vh;
    }
    
    .welcomePageTitle {
        font-size: 20px;
    }
    
    .signIn {
        width: 100%;
        height: 90vh;
    }
}


